import React from 'react';
import './style.css';

const CurrentWheather = ({name, temp, humidity, pressure, visibility, wind, clouds, icon}) => {
    return(
        <div className="current__weather">
                <p className="current__weather-name">Current weather</p>
                <p className="current__weather-field"> : <b>{name}</b></p>
                <p className="current__weather-field">: <b>{temp}&deg;</b></p>
                <p className="current__weather-field">: <b>{humidity}%</b></p>
                <p className="current__weather-field">: <b>{pressure} </b></p>
                <p className="current__weather-field">: <b>{visibility}</b></p>
                <p className="current__weather-field"> : <b>{wind} /</b></p>
                <p className="current__weather-field">: <b>{clouds}</b></p>
                <p className="current__weather-field"><img className="WheatherPic" src={icon} alt="wheather_icon" /></p>
        </div>
    );
}

export default CurrentWheather;


