import React from 'react';
import './style.css'

const ForecastWheather = ({date, name, temp, humidity, pressure, wind, clouds, icon}) => {
    return(
        <div className="ForecastWheather">
            <p className="ForecastWheather__data">{date}</p>
            <p className="ForecastWheather__field">: <b>{temp}&deg;</b></p>
            <p className="ForecastWheather__field">: <b>{humidity}%</b></p>
            <p className="ForecastWheather__field">: <b>{pressure} </b></p>
            <p className="ForecastWheather__field"> : <b>{wind} /</b></p>
            <p className="ForecastWheather__field">: <b>{clouds}</b></p>
            <p className="ForecastWheather__field"><img className="WheatherPic" src={icon} alt="wheather_icon" /></p>
        </div>
    );
}

export default ForecastWheather;


